.App {
  padding: 1em;
}

.simple-notification-component-wrapper {
  margin: 0.5em auto;
  padding: 0.7em;
  width: 90%;
  max-width: 700px;
  display: flex;
  justify-content: flex-start;
  border-radius: 0.5em;
}
.simple-notification-component-wrapper.active {
  background-color: hsl(211deg, 68%, 94%);
}
.simple-notification-component-wrapper .user-avatar-thumbnail {
  width: 50px;
  height: 50px;
  margin-right: 1em;
}
.simple-notification-component-wrapper .notification-content {
  display: flex;
  flex-direction: column;
}
.simple-notification-component-wrapper .notification-content .user-name {
  margin-right: 0.2em;
}
.simple-notification-component-wrapper .notification-content .notification-message {
  color: hsl(219deg, 14%, 63%);
}
.simple-notification-component-wrapper .notification-content .active-notification {
  width: 8px;
  height: 8px;
  border-radius: 50%;
}
.simple-notification-component-wrapper .notification-content .active-notification.active {
  background-color: hsl(1deg, 90%, 64%);
}
.simple-notification-component-wrapper .notification-content .notification-time {
  display: flex;
  color: hsl(219deg, 14%, 63%);
}/*# sourceMappingURL=style.css.map */